@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,400&display=swap");
body {
    margin: 0;
    font-family: "Montserrat", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #14171e;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f2f5;
}

.bg-dark-alt {
    background: #20232d;
}

.bg-dark {
    background: #14171e;
}

.bg-primary {
    background: #597eea;
}

.icon-bg {
    background: rgba(89, 126, 234, 0.19);
    color: #597eea;
}

.dark-text-color {
    color: #dde3ee;
}

.text-primary {
    color: #597eea;
}

.primary-border-color {
    border-color: #597eea;
}

.button-primary {
    background: #4c6ef5;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;
    text-align: center;
    border-radius: 100em;
    padding: 10px 24px;
    transition: all 300ms ease-in-out;
}

.button-primary:hover {
    background: #597eea;
}

.chat-container {
    position: relative;
    width: 100%;
    height: auto;
    min-height: calc(100vh - 100px);
    display: flex;
    flex-direction: column;
    background-color: #20232d;
    border-radius: 20px;
    overflow: hidden;
}

.chat-header-and-messages {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    overflow: hidden;
}

.chat-header {
    min-height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: bold;
    border-top-right-radius: 16px;
    border-top-left-radius: 16px;
    z-index: 2;
    background: #333848;
    color: #dde3ee;
    border-radius: 0 0 90% 90% / 10px;
    flex-shrink: 0;
}

.chat-inner {
    flex-grow: 1;
    overflow: auto;
    padding: 20px;
    position: relative;
}

.chat-messages {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 100%;
    max-width: 100%;
    padding: 20px;
    padding-bottom: 80px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
}

.botname {
    font-weight: bold;
    color: rgba(255, 255, 255, 0.4);
}

.chat-message {
    margin-bottom: 16px;
    width: 100%;
    display: flex;
}

.chat-message.user {
    justify-content: flex-end;
}

.chat-message.user .message-content {
    position: relative;
    width: auto;
    color: rgba(221, 227, 238, 0.9);
    background: #597eea;
    padding: 15px 20px;
    float: right;
    border-top-right-radius: 6px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    overflow-wrap: break-word;
}

.chat-message.bot {
    justify-content: flex-start;
    flex-direction: column;
}

.chat-message.bot .message-content {
    position: relative;
    width: auto;
    color: rgba(221, 227, 238, 0.7);
    border-top-right-radius: 30px;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    background-color: #333848;
    padding: 20px;
    float: left;
    overflow-wrap: break-word;
}

.chat-message .message-content {
    text-align: left;
    max-width: 75%;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
}

.chat-input button svg {
    margin: 0;
}

.chat-input-wrapper {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 16px;
    border-bottom-left-radius: 16px;
    background: #333848;
    box-shadow: 0px -4px 20px rgba(21, 23, 30, 0.46);
}

.chat-input {
    display: flex;
    padding: 16px;
}

.chat-input input {
    flex: 1 1;
    border: none;
    padding-left: 20px;
    border-radius: 20px;
    margin-right: 20px;
    background-color: #20232d;
    color: rgba(255, 255, 255, 0.7);
}

.chat-input input:focus {
    box-shadow: none;
}

.chat-input button {
    padding: 0;
    color: #597eea;
    border-radius: 100em;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(89, 126, 234, 0.19);
    transition: all 300ms ease;
}

.chat-input button:hover {
    color: rgba(255, 255, 255, 0.7);
    background: #597eea;
}

.chat-header-inner {
    display: flex;
    align-items: center;
    justify-content: center;
}

.chat-header-inner .ai-avatar img {
    border-radius: 10px;
    overflow: hidden;
    height: 35px;
    width: 35px;
}

.chat-header-inner .ai-name {
    font-size: 18px;
    text-align: left;
    margin-left: 10px;
    font-weight: 700;
    line-height: 1;
    color: rgba(255, 255, 255, 0.7);
}

.ai-status {
    font-size: 12px;
    opacity: 0.7;
    display: flex;
    align-items: center;
    margin-top: 3px;
    font-weight: 400;
}

.ai-status-circle {
    background: rgb(0, 255, 0);
    height: 5px;
    width: 5px;
    border-radius: 100em;
    margin-left: 5px;
}

.chat-message.bot .avatar {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #6a7280;
    font-weight: 500;
    margin-bottom: 10px;
}

.chat-message.bot .avatar img {
    width: 20px;
    height: 20px;
    border-radius: 100em;
    margin-right: 5px;
}

.chat-input-wrapper a {
    font-size: 10px;
    color: #3a53c5;
    font-weight: 600;
    text-align: left;
    padding-top: 20px;
    display: flex;
    justify-content: center;
    padding: 0 0 8px;
    text-decoration: underline;
}

@media screen and (max-width: 768px) {
    .chat-input button {
        width: 40px;
        height: 40px;
        min-width: 40px;
    }
    .chat-input input {
        margin-right: 10px;
    }
    .chat-message .message-content {
        font-size: 12px;
        line-height: 18px;
    }
}